import React, { useState, useCallback } from 'react';
import { Toaster, toast } from 'react-hot-toast';
import FileUploader from './components/ImageUploader';
import OCRProcessor from './components/OCRProcessor';
import ExcelGenerator from './components/ExcelGenerator';
import TemplateManager from './components/TemplateManager';
import Header from './components/Header';

interface Template {
  filename: string;
  originalname: string;
  size: number;
  uploadTime: string;
  isDefault?: boolean;
  regions?: any[];
}

interface OCRResult {
  fullText: string;
  regions: any[];
  confidence: number;
}

function App() {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [fileUrl, setFileUrl] = useState<string | null>(null);
  const [ocrResult, setOcrResult] = useState<OCRResult | null>(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [selectedTemplate, setSelectedTemplate] = useState<Template | null>(null);
  const [uploadResult, setUploadResult] = useState<any>(null);

  const handleFileSelect = useCallback((file: File, url: string) => {
    setSelectedFile(file);
    setFileUrl(url);
    setOcrResult(null);
    setUploadResult(null);
    setUploading(true);
    toast.success('文件选择成功！');
  }, []);

  const handleUploadComplete = useCallback((result: any) => {
    setUploading(false);
    setUploadResult(result);
    
    // 如果是PDF文件且有提取的文本，显示提示
    if (result.file.mimetype === 'application/pdf') {
      if (result.file.extractedText) {
        toast.success('PDF文本提取成功！');
      } else if (result.file.pdfError) {
        toast.error(result.file.pdfError);
      }
    }
  }, []);

  const handleOCRComplete = useCallback((result: OCRResult) => {
    setOcrResult(result);
    toast.success('文件识别完成！');
  }, []);

  const handleProcessingStart = useCallback(() => {
    setIsProcessing(true);
  }, []);

  const handleProcessingEnd = useCallback(() => {
    setIsProcessing(false);
  }, []);

  const handleReset = useCallback(() => {
    setSelectedFile(null);
    setFileUrl(null);
    setOcrResult(null);
    setIsProcessing(false);
    setUploading(false);
    setUploadResult(null);
    toast.success('已重置，可以上传新文件');
  }, []);

  const handleTemplateSelect = useCallback((template: Template | null) => {
    setSelectedTemplate(template);
    if (template) {
      if (template.isDefault) {
        toast.success(`已启用系统缺省模板：${template.originalname}`);
      } else {
        toast.success(`已选择模板：${template.originalname}`);
      }
    }
  }, []);

  return (
    <div className="min-h-screen bg-gray-50">
      <Toaster 
        position="top-right"
        toastOptions={{
          duration: 3000,
          style: {
            background: '#363636',
            color: '#fff',
          },
        }}
      />
      
      <Header />
      
      <main className="container mx-auto px-4 py-8">
        <div className="max-w-7xl mx-auto">
          {/* 进度指示器 */}
          <div className="mb-8">
            <div className="flex items-center justify-center space-x-4">
              <div className={`flex items-center space-x-2 ${selectedFile ? 'text-green-600' : 'text-gray-400'}`}>
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${selectedFile ? 'bg-green-100' : 'bg-gray-100'}`}>
                  <span className="text-sm font-semibold">1</span>
                </div>
                <span className="text-sm">上传文件</span>
              </div>
              <div className={`w-16 h-1 ${selectedFile ? 'bg-green-200' : 'bg-gray-200'}`}></div>
              <div className={`flex items-center space-x-2 ${ocrResult ? 'text-green-600' : selectedFile ? 'text-blue-600' : 'text-gray-400'}`}>
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${ocrResult ? 'bg-green-100' : selectedFile ? 'bg-blue-100' : 'bg-gray-100'}`}>
                  <span className="text-sm font-semibold">2</span>
                </div>
                <span className="text-sm">识别文件</span>
              </div>
              <div className={`w-16 h-1 ${ocrResult ? 'bg-green-200' : 'bg-gray-200'}`}></div>
              <div className={`flex items-center space-x-2 ${ocrResult ? 'text-blue-600' : 'text-gray-400'}`}>
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${ocrResult ? 'bg-blue-100' : 'bg-gray-100'}`}>
                  <span className="text-sm font-semibold">3</span>
                </div>
                <span className="text-sm">输出文件</span>
              </div>
            </div>
          </div>

          <div className="grid grid-cols-1 xl:grid-cols-3 gap-8">
            {/* 左侧：文件上传 */}
            <div className="space-y-6">
              <FileUploader
                onFileSelect={handleFileSelect}
                onUploadComplete={handleUploadComplete}
                uploading={uploading}
              />

              {/* 文件信息显示 */}
              {uploadResult && (
                <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
                  <div className="p-4 border-b border-gray-200">
                    <h3 className="text-lg font-medium text-gray-900">文件信息</h3>
                  </div>
                  <div className="p-4 space-y-2">
                    <p className="text-sm">
                      <span className="font-medium text-gray-900">文件名：</span>
                      <span className="text-gray-600">{uploadResult.file.originalname}</span>
                    </p>
                    <p className="text-sm">
                      <span className="font-medium text-gray-900">文件类型：</span>
                      <span className="text-gray-600">
                        {uploadResult.file.mimetype === 'application/pdf' ? 'PDF文档' : '图片文件'}
                      </span>
                    </p>
                    <p className="text-sm">
                      <span className="font-medium text-gray-900">文件大小：</span>
                      <span className="text-gray-600">
                        {(uploadResult.file.size / 1024 / 1024).toFixed(2)} MB
                      </span>
                    </p>
                    {uploadResult.file.pageCount && (
                      <p className="text-sm">
                        <span className="font-medium text-gray-900">页数：</span>
                        <span className="text-gray-600">{uploadResult.file.pageCount} 页</span>
                      </p>
                    )}

                    {/* PDF扫描版检测和建议 */}
                    {uploadResult.file.mimetype === 'application/pdf' && uploadResult.file.pdfError && (
                      <div className="mt-4 p-3 bg-red-50 border border-red-200 rounded-lg">
                        <h4 className="text-sm font-medium text-red-800 mb-2">⚠️ 扫描版PDF检测</h4>
                        <p className="text-xs text-red-700 mb-3">{uploadResult.file.pdfError}</p>
                        <div className="text-xs text-red-700">
                          <p className="font-medium mb-2">💡 解决方案：</p>
                          <div className="bg-white p-3 rounded border space-y-2">
                            <div className="flex items-start space-x-2">
                              <span className="font-medium text-blue-600">1.</span>
                              <span>将PDF页面截图保存为JPG/PNG格式</span>
                            </div>
                            <div className="flex items-start space-x-2">
                              <span className="font-medium text-blue-600">2.</span>
                              <span>使用PDF阅读器：文件 → 导出 → 图像</span>
                            </div>
                            <div className="flex items-start space-x-2">
                              <span className="font-medium text-blue-600">3.</span>
                              <span>重新上传图片文件，系统将自动OCR识别</span>
                            </div>
                            <div className="flex items-start space-x-2">
                              <span className="font-medium text-green-600">✓</span>
                              <span>支持意大利文、英文、中文等多语言识别</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    )}

                    {/* PDF成功解析 */}
                    {uploadResult.file.mimetype === 'application/pdf' && uploadResult.file.extractedText && !uploadResult.file.pdfError && (
                      <div className="mt-4 p-3 bg-green-50 border border-green-200 rounded-lg">
                        <h4 className="text-sm font-medium text-green-800 mb-2">✓ PDF文本提取成功</h4>
                        <p className="text-xs text-green-700">
                          已成功提取 {uploadResult.file.extractedText.length} 个字符的文本内容
                        </p>
                      </div>
                    )}
                  </div>
                </div>
              )}
            </div>

            {/* 中间：文件识别 */}
            <div className="space-y-6">
              {selectedFile && (
                <OCRProcessor
                  image={selectedFile}
                  onOCRComplete={handleOCRComplete}
                  onProcessingStart={handleProcessingStart}
                  onProcessingEnd={handleProcessingEnd}
                  extractedText={uploadResult?.file?.extractedText}
                  fileType={uploadResult?.file?.mimetype}
                  template={selectedTemplate}
                />
              )}
            </div>

            {/* 右侧：文件输出 */}
            <div className="space-y-6">
              {ocrResult && (
                <ExcelGenerator
                  ocrResult={ocrResult}
                  originalImage={selectedFile}
                  selectedTemplate={selectedTemplate}
                  onReset={handleReset}
                />
              )}
            </div>
          </div>

          {/* 使用说明 */}
          {!selectedFile && (
            <div className="mt-12 bg-white rounded-lg shadow-sm p-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">使用说明</h3>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm text-gray-600">
                <div className="flex items-start space-x-2">
                  <div className="w-6 h-6 rounded-full bg-blue-100 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-xs font-semibold text-blue-600">1</span>
                  </div>
                  <div>
                    <p className="font-medium">上传文件</p>
                    <p>支持图片文件（JPG、PNG、GIF等）和PDF文档</p>
                  </div>
                </div>
                <div className="flex items-start space-x-2">
                  <div className="w-6 h-6 rounded-full bg-blue-100 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-xs font-semibold text-blue-600">2</span>
                  </div>
                  <div>
                    <p className="font-medium">识别文件</p>
                    <p>自动识别文件内容，支持中英文，可手动编辑</p>
                  </div>
                </div>
                <div className="flex items-start space-x-2">
                  <div className="w-6 h-6 rounded-full bg-blue-100 flex items-center justify-center flex-shrink-0 mt-0.5">
                    <span className="text-xs font-semibold text-blue-600">3</span>
                  </div>
                  <div>
                    <p className="font-medium">输出文件</p>
                    <p>生成标准Excel文件，支持自定义格式和模板</p>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* 模板管理 - 移到底部 */}
          <div className="mt-16 border-t border-gray-200 pt-8">
            <div className="mb-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-2">高级设置</h3>
              <p className="text-sm text-gray-500">
                可选：配置自定义模板和输出格式（适用于重复性工作）
              </p>
            </div>
            <TemplateManager
              onTemplateSelect={handleTemplateSelect}
              selectedTemplate={selectedTemplate}
            />
          </div>
        </div>
      </main>
    </div>
  );
}

export default App;
